import React from 'react';
import './css/footerAccordion.css';
import {Accordion} from 'react-bootstrap';


class FooterAccordion extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			expendedM: false,
			sizeWidth: true,
		}
	}

	getWindowDimensions = ()=> {
		const { innerWidth: width } = window;
		return width;
	}

	handleWindowDimensions = ()=> {
		let width = this.getWindowDimensions()
		this.setState({
			sizeWidth: width > 886
		})
	}

	componentDidMount() {
		this.handleWindowDimensions();
		window.addEventListener('resize', this.handleWindowDimensions);
	}

	componentWillUnmount() {
		window.removeEventListener('resize', this.handleWindowDimensions);
	}

	render(){
		const{sizeWidth} = this.state;
		if(sizeWidth === true){
			return(
				<div className='footer__body-accordion'>
					<div className='footer__body-accordion-infoandstore'>
						<nav>
							<h3></h3>
							<ul>
								<li><a href="#"> </a></li>
								<li><a href="#"></a></li>
								<li><a href="#"></a></li>
								<li><a href="#"></a></li>
							</ul>
						</nav>
					</div>
					<div className='footer__body-accordion-infoandstore'>
						<nav>
							<h3>-</h3>
							<ul>
								<li><a href="#"></a></li>
								<li><a href="#"></a></li>
								<li><a href="#">-</a></li>
								<li><a href="#"></a></li>
							</ul>
						</nav>
					</div>
				</div>
			)
		} else {
			return(
				<div className='footer__body-accordion-phone'>
					<Accordion>
						<Accordion.Item eventKey="0">
							<Accordion.Header></Accordion.Header>
							<Accordion.Body>
								<ul>
									<li><a href="#"> </a></li>
									<li><a href="#"></a></li>
									<li><a href="#"></a></li>
									<li><a href="#"></a></li>
								</ul>
							</Accordion.Body>
						</Accordion.Item>
						<Accordion.Item eventKey="1">
							<Accordion.Header>-</Accordion.Header>
							<Accordion.Body>
								<ul>
									<li><a href="#"></a></li>
									<li><a href="#"></a></li>
									<li><a href="#">-</a></li>
									<li><a href="#"></a></li>
								</ul>
							</Accordion.Body>
						</Accordion.Item>
					</Accordion>
				</div>
			)
		}
		
	}
}

export default FooterAccordion;

